// 课堂满意度
import React, { Component } from 'react';
import { Col } from 'antd';
import OtherChart from '../../../common/components/BaseEcharts/OtherChart';
// import jsonData from '../../../data/course/课程视角--教学评价--课堂满意度.json';
import { getAction } from '../../../axios';
export default class ClassSatisfied extends Component {
  constructor(props) {
    super(props);
    this.state = {
      indicator: [],
      data: [],
    }
  }
  componentDidMount(){
    this.getData(this.props.currentLevel2Menu);
  }
  componentWillReceiveProps(nextProps){
    if(JSON.stringify(this.props) !== JSON.stringify(nextProps)){
     this.getData(nextProps.currentLevel2Menu);
    }
 }
  getData(currentLevel2Menu){
    getAction('/bigdata/course/condition/info/queryCourseClassroomStudyInfo', {
      courseCode: JSON.parse(currentLevel2Menu).code
    }).then(res => {
      if(res.success && res.obj !== null && JSON.stringify(res.obj) !== '{}'){
        let indicator = [];
        res.obj.axis && res.obj.axis.forEach(item => {
          indicator.push({
            name: item,
            max: 100
          })
        })
        this.setState({
          indicator,
          data: res.obj.data,
        })
      }
    })
  }
  render() {
    let {data, indicator} = this.state
    let option = {
      title: {
        show: false,
      },
      grid: {
        top: '50%',
        // left: '3%',
        // right: '3%',
        // bottom: '25%',
        containLabel: false,
      },
      tooltip: {},
      legend: {
        show: false,
      },
      radar: {
        center: ['50%', '55%'],
        axisLine: {
          lineStyle: {
            color: '#00CFFF',
          },
        },
        name: {
          textStyle: {
            color: '#00CFFF',
            borderRadius: 3,
            padding: [0, 0],
          },
        },
        nameGap :5,
        indicator,
      },
      series: [{
        name: '',
        type: 'radar',
        areaStyle: {
          normal: {
            color: '#00CFFF',
          },
        },
        lineStyle: {
          color: '#00CFFF',
        },
        symbolSize:0,
        data: [
          {
            value: data,
            name: '课堂满意度',
          },
        ],
      }],
    };
    return (
      <Col span={24} style={{height:'100%'}}>
        <OtherChart
          id='ClassSatisfied'
          data={option.radar.indicator[0]?option:null}
          height='100%'/>
      </Col>
    );
  }
}
