import React, {Component} from 'react';
import {Col} from 'antd';
import "../styles.less"
import BarEcharts from '../../../common/components/BaseEcharts/BarEcharts';
import echarts from 'echarts';
import { getAction } from '../../../axios';
// import jsonData from '../../../data/course/课程视角--课程指数及排名.json';

export default class CurriculumIndexRanking extends Component {
  constructor(props) {
    super(props);
    this.state = {
      axis: [],
      data: [],
    }
  }
  componentDidMount(){
    this.getData(this.props.currentLevel2Menu);
  }
  componentWillReceiveProps(nextProps){
    if(JSON.stringify(this.props) !== JSON.stringify(nextProps)){
     this.getData(nextProps.currentLevel2Menu);
    }
 }
  getData(currentLevel2Menu){
    getAction('/bigdata/course/condition/info/queryCourseRankingIndexInfo', {
      courseCode: JSON.parse(currentLevel2Menu).code
    }).then(res => {
      if(res.success && res.obj !== null && JSON.stringify(res.obj) !== '{}'){
          this.setState({
            axis: res.obj.axis,
            data: res.obj.data
          })
      }
    })
  }
  render(){
    let option = {
      title: {
        show:false
      },
      xAxis: {
        data: this.state.axis,
        axisLabel: {
          interval:0,
          textStyle: {
            color: '#00CFFF'
          }
        }
      },
      yAxis: {
        axisLine: {
          show: false
        },
        axisTick: {
          show: false
        },
        axisLabel: {
          show:false
        },
        splitLine:{
          // show:false,
          lineStyle: {
            color: "#379fb7",
            type: "dashed"
          }
        }
      },
      series: [
        {
          type: 'bar',
          itemStyle: {
            normal: {
              color: new echarts.graphic.LinearGradient(
                0, 0, 0, 1,
                [
                  {offset: 0, color: 'rgba(0,207,255,1)'},
                  {offset: 0.5, color: 'rgba(0,207,255,0.8)'},
                  {offset: 1, color: 'rgba(0,207,255,0.19)'}
                ]
              )
            }
          },
          label: {
            normal: {
                show: true,
                position: 'top',
                color: '#FFF',
            }
          },
          data: this.state.data
        }
      ]
    }
    return(
      <Col span={24} style={{height:'100%'}}>
        <BarEcharts
            id='indexRankingId'
            data={option}
            height='100%'/>
      </Col>
    )
  }
}
