// 基本情况
import React, { Component } from 'react';
import { Row, Col } from 'antd';
import '../styles.less'
import { getAction } from '../../../axios';
export default class BasicInfo extends Component {
  constructor(props){
    super(props);
    this.state = {
        code:'',
        specialty:'',
        studyTime:'',
        keepTime:'',
        classType:'',
        property:'',
        classGroup:'',
        peopleNum:''
    }
  }
  componentDidMount(){
    this.getData(this.props.currentLevel2Menu);
  }
  componentWillReceiveProps(nextProps){
    if(JSON.stringify(this.props) !== JSON.stringify(nextProps)){
     this.getData(nextProps.currentLevel2Menu);
    }
 }
  getData(currentLevel2Menu){
    getAction('/bigdata/course/condition/info/queryBasicCourseInfo', {
      courseCode: JSON.parse(currentLevel2Menu).code
    }).then(res => {
      if(res.success && res.obj !== null && JSON.stringify(res.obj) !== '{}'){
          this.setState({
            code: res.obj.code || '',
            specialty:res.obj.specialty || '',
            studyTime:res.obj.studyTime || '',
            keepTime:res.obj.keepTime || '',
            classType:res.obj.classType || '',
            property:res.obj.property || '',
            classGroup:res.obj.classGroup || '',
            peopleNum:res.obj.peopleNum || ''
          })
      }
    })
  }
  render() {
    return (
      <Row type="flex" align="middle" style={{paddingLeft:'10%'}}>
        <Col span={24}  style={{margin:0}} className={'major_visual_basic_info_title'}>汽车构造与商务评价（三）</Col>
        <Col span={24} className={'major_visual_basic_info_content'}>
          <div><label>课程代码：</label><span>{this.state.code}</span></div>
          <div><label>适用专业：</label><span>{this.state.specialty}</span></div>
          <div>
              <label>总学时：</label><span>{this.state.studyTime}</span>
              <label style={{marginLeft:'20px'}}>周学时：</label><span>{this.state.keepTime}</span>
          </div>
          <div>
              <label>课程类型：</label><span>{this.state.classType}</span>
              <label style={{marginLeft:'20px'}}>课程性质：</label><span>{this.state.property}</span>
          </div>
          <div>
              <label>课程组：</label><span>{this.state.classGroup}</span>
              <label style={{marginLeft:'20px'}}>课程组人数：</label><span>{this.state.peopleNum}</span>
          </div>
        </Col>
      </Row>
    )
  }
}
