import React, { Component } from 'react'
import { Carousel} from 'antd'
import { connect } from 'react-redux';
import BasicInfo from './components/BasicInfo'
import SchoolBeginsCondition from './components/SchoolBeginsCondition'
import CurriculumIndexRanking from './components/CurriculumIndexRanking'
import CourseTeaching from './components/CourseTeaching'
import InstructionalResource from './components/InstructionalResource'
import PracticeTeaching from './components/PracticeTeaching'
import ClassSatisfied from './components/ClassSatisfied'
import ExaminationResults from './components/ExaminationResults'

// import Chuangyi from './components/chuangyi'
// import Danpianji from './components/danpianji'
// import Dianzishangwu from './components/dianzishangwu'
// import Fadongji from './components/fadongji'
// import Jianzhu from './components/jianzhu'
// import Jisuanji from './components/jisuanji'
// import Jixie from './components/jixie'
// import Kuaiji from './components/kuaiji'
// import Shukong from './components/shukong'
// import Sixiang from './components/sixiang'
// import Sousuo from './components/sousuo'
// import Zhongguo from './components/zhongguo'
// import Zonghe from './components/zonghe'

import BarchartComponent from './barchartComponent';
import jsonData from './jsonData';

class Kecheng extends Component {
  render() {
    let autoplaySpeed = 40000;
        const settings = {
            // autoplay: true,
            autoplaySpeed,
            dots: true,
            infinite: false,
            slidesToShow: 1,
            slidesToScroll: 1,
            vertical: true,
            verticalSwiping: true,
            beforeChange: function (currentSlide, nextSlide) {
                // console.log("before change", currentSlide, nextSlide);
            },
            afterChange: (currentSlide) => {
                if (currentSlide === 4) {
                    setTimeout(() => {
                        if (this.slider && this.slider.goTo) {
                            this.slider.goTo(0)
                        }
                    }, autoplaySpeed)
                }
            },
        };
        let { currentLevel2Menu } = this.props;
    return (
      <Carousel ref={slider => (this.slider = slider)} {...settings} >
        <div>     
        <div style={{ height:'calc(100vh - 74px)',overflowY:'auto'}}>     
          <div className='item-wrapper'>
            <div className='item-box'>
              <div className='item-title'>
                <span>
                基本情况
                            </span>

              </div>
              <div className='item-chart'>
              <BasicInfo currentLevel2Menu={currentLevel2Menu}></BasicInfo>
              </div>              
            </div>
          </div>
          <div className='item-wrapper-2'>
            <div className='item-box'>
              <div className='item-title'>
                <span>
                开课情况
                            </span>

              </div>
              <div className='item-chart'>
              <SchoolBeginsCondition currentLevel2Menu={currentLevel2Menu}></SchoolBeginsCondition>
              </div>              
            </div>
          </div>
          <div className='item-wrapper'>
            <div className='item-box'>
              <div className='item-title'>
                <span>
                课程指数及排名
                            </span>

              </div>
              <div className='item-chart'>
              <CurriculumIndexRanking currentLevel2Menu={currentLevel2Menu}></CurriculumIndexRanking>
              </div>              
            </div>
          </div>
             

          <div className='item-wrapper-3'>
            <div className='item-box'>
              <div className='item-title'>
                <span>
                课程教学情况
                            </span>

              </div>
              <div className='item-chart'>
              <CourseTeaching currentLevel2Menu={currentLevel2Menu}></CourseTeaching>
              </div>              
            </div>
          </div>   
          
          <div className='item-wrapper'>
            <div className='item-box'>
              <div className='item-title'>
                <span>
                教学资源情况
                            </span>

              </div>
              <div className='item-chart'>
              <InstructionalResource currentLevel2Menu={currentLevel2Menu}></InstructionalResource>
              </div>              
            </div>
          </div>  
          <div className='item-wrapper'>
            <div className='item-box'>
              <div className='item-title'>
                <span>
                实训教学

                            </span>

              </div>
              <div className='item-chart'>
              <PracticeTeaching currentLevel2Menu={currentLevel2Menu}/>
              </div>              
            </div>
          </div>   
         
          <div className='item-wrapper-2'>
            <div className='item-box'>
              <div className='item-title'>
                <span>
                教学评价-考试成绩
                            </span>

              </div>
              <div className='item-chart'>
              <ExaminationResults currentLevel2Menu={currentLevel2Menu}/>
              </div>              
            </div>
          </div>   
          <div className='item-wrapper'>
            <div className='item-box'>
              <div className='item-title'>
                <span>
                教学评价-课堂满意度
                            </span>

              </div>
              <div className='item-chart'>
              <ClassSatisfied currentLevel2Menu={currentLevel2Menu}/>
              </div>              
            </div>
          </div>  
           
          {
            jsonData.map((item, i) => {
              if(item.type === 'bar'){
                return <BarchartComponent currentLevel2Menu={currentLevel2Menu} data={item} key={i}/>
              }else {
                return null;
              }
            })
          }

          {/* <Danpianji></Danpianji>
          <Chuangyi></Chuangyi>
          
          <Dianzishangwu></Dianzishangwu>
          <Fadongji></Fadongji>
          <Jianzhu></Jianzhu>
          <Jisuanji></Jisuanji>
          <Jixie></Jixie>
          <Kuaiji></Kuaiji>
          <Shukong></Shukong>
          <Sixiang></Sixiang>
          <Sousuo></Sousuo>
          <Zhongguo></Zhongguo>
          <Zonghe></Zonghe> */}
        </div>
        </div>

      </Carousel>
    )
  }
}

export default connect((state)=> {
  return {
    currentLevel2Menu: state.level2MenuReducer.currentLevel2Menu
  }
})(Kecheng)
