// 实践教学
import React, { Component } from 'react';
import { Col } from 'antd';
import BarEcharts from '../../../common/components/BaseEcharts/BarEcharts';
import {getAction} from '../../../axios';
// import jsonData from '../../../data/course/课程视角--实训教学.json';

export default class PracticeTeaching extends Component {
  constructor(props) {
    super(props);
    this.state = {
      data: []
    }
  }
  componentDidMount(){
    this.getData(this.props.currentLevel2Menu);
  }
  componentWillReceiveProps(nextProps){
    if(JSON.stringify(this.props) !== JSON.stringify(nextProps)){
     this.getData(nextProps.currentLevel2Menu);
    }
 }
  getData(currentLevel2Menu){
    getAction('/bigdata/course/condition/info/queryCoursePracticalTrainingCondition', {
      courseCode: JSON.parse(currentLevel2Menu).code
    }).then(res => {
      if(res.success && res.obj !== null){
        this.setState({
          data: res.obj
        })
      }
    })
  }
  render() {
    let {data} = this.state;
    let option = {
      title: {
        show: false,
      },
      xAxis: {
        data: data[0] && data[0]['value'].map(item => {
          return item.name
        }),
        axisLabel: {
          interval: 0,
          textStyle: {
            color: '#00CFFF',
          },
        },
      },
      legend: {
        icon: 'circle',
        itemWidth: 14,
        itemHeight: 5,
        itemGap: 13,
        bottom: '4%',
        textStyle: {
          fontSize: 12,
          color: '#00cfff'
        }
      },
      grid: {
        top: '10%',
        left: '3%',
        right: '3%',
        bottom: '15%',
        containLabel: true
      },
      color: ['#00CFFF','#BD10E0'],
      yAxis: {
        axisLine: {
          show: false,
        },
        axisTick: {
          show: false,
        },
        axisLabel: {
          show: false,
        },
        splitLine: {
          lineStyle: {
            color: '#379fb7',
            type: 'dashed',
          },
        },
      },
      series: data[0] && data.map(item => {
        return {
          name: item.name,
          type: 'bar',
          barGap: 0,
          data: item['value'][0] && item['value'].map(ele => {
            return ele.value;
          }),
          label: {
            normal: {
              color: '#fff',
              show: true,
              position: 'top',
            },
          },
        }
      }),
    };
    return (
      <Col span={24} style={{height:'100%'}}>
        <BarEcharts
          id='PracticeTeachingBar'
          data={option}
          height='100%'/>
      </Col>
    );
  }
}
