// 考试成绩
import React, { Component } from 'react';
import { Col } from 'antd';
import {getAction} from '../../../axios';
import LineEcharts from '../../../common/components/BaseEcharts/LineEcharts';
import BarEcharts from '../../../common/components/BaseEcharts/BarEcharts';
// import jsonData from '../../../data/course/课程视角--考试成绩--考试成绩1.json';
// import jsonData1 from '../../../data/course/课程视角--考试成绩--考试成绩2.json';

export default class ExaminationResults extends Component {
  constructor(props) {
    super(props);
    this.state = {
      data1: [],
      data2: []
    }
  }
  componentDidMount(){
    this.getData1(this.props.currentLevel2Menu);
    this.getData2(this.props.currentLevel2Menu);
  }
  componentWillReceiveProps(nextProps){
    if(JSON.stringify(this.props) !== JSON.stringify(nextProps)){
     this.getData1(nextProps.currentLevel2Menu);
     this.getData2(nextProps.currentLevel2Menu);
    }
 }
  getData1(currentLevel2Menu){
    getAction('/bigdata/course/condition/info/queryCourseExamResultSituation1', {
      courseCode: JSON.parse(currentLevel2Menu).code
    }).then(res => {
      if(res.success && res.obj !== null){
        this.setState({
          data1: res.obj
        })
      }
    })
  }
  getData2(currentLevel2Menu){
    getAction('/bigdata/course/condition/info/queryCourseExamResultSituation2', {
      courseCode: JSON.parse(currentLevel2Menu).code
    }).then(res => {
      if(res.success && res.obj !== null){
        this.setState({
          data2: res.obj
        })
      }
    })
  }
  render() {
    let {data1, data2} = this.state;
    let lineOption = {
      title: {
        show: false
      },
      tooltip: {
        trigger: 'axis'
      },
      legend: {
        icon: 'circle',
        itemWidth: 14,
        itemHeight: 5,
        itemGap: 13,
        bottom: '4%',
        textStyle: {
          fontSize: 12,
          color: '#00cfff'
        }
      },
      grid: {
        top: '10%',
        left: '3%',
        right: '3%',
        bottom: '15%',
        containLabel: true
      },
      toolbox: {
        feature: {
          saveAsImage: {}
        }
      },
      xAxis: {
        axisLabel: {
          interval:0,
          textStyle: {
            color: '#00CFFF'
          }
        },
        data: data1[0] && data1[0]['value'].map(item => {
          return item.name
        })
      },
      yAxis: {
        axisLine: {
          show: false
        },
        axisTick: {
          show: false
        },
        axisLabel: {
          color:'#00CFFF'
        },
        splitLine:{
          // show:false,
          lineStyle: {
            color: "#379fb7",
            type: "dashed"
          }
        }
      },
      color:['#30CCF4','#DED324'],
      series: data1[0] && data1.map(item => {
        return {
          name: item.name,
          type: 'line',
          data: item['value'][0] && item['value'].map(ele => {
            return ele.value;
          }),
        }
      })
    };
    let option = {
      title: {
        show: false,
      },
      xAxis: {
        data: data2[0] && data2[0]['value'].map(item => {
          return item.name
        }),
        axisLabel: {
          interval: 0,
          textStyle: {
            color: '#00CFFF',
          },
        },
      },
      legend: {
        icon: 'circle',
        itemWidth: 14,
        itemHeight: 5,
        itemGap: 13,
        bottom: '4%',
        textStyle: {
          fontSize: 12,
          color: '#00cfff'
        }
      },
      grid: {
        top: '10%',
        left: '3%',
        right: '3%',
        bottom: '15%',
        containLabel: true
      },
      color: ['#00CFFF','#BD10E0'],
      yAxis: {
        axisLine: {
          show: false,
        },
        axisTick: {
          show: false,
        },
        axisLabel: {
          show: false,
        },
        splitLine: {
          lineStyle: {
            color: '#379fb7',
            type: 'dashed',
          },
        },
      },
      series: data2[0] && data2.map(item => {
        return {
          name: item.name,
          type: 'line',
          data: item['value'][0] && item['value'].map(ele => {
            return ele.value;
          }),
        }
      })
    };
    return (
      <Col span={24} style={{height:'100%'}}>
        <Col span={12} style={{height:'100%'}}>
          <LineEcharts
            id='ExaminationResultsLine'
            data={lineOption}
            height='100%'/>
        </Col>
        <Col span={12} style={{height:'100%'}}>
          <BarEcharts
            id='ExaminationResultsBar'
            data={option}
            height='100%'/>
        </Col>
      </Col>
    );
  }
}
